
#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    margin-top: 60px;
}
#address {
    margin-top: 15px;
}
.mask {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.6);
}
.popup {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    min-width: 200px;
    min-height: 200px;
    background-color: #fff;
    border-radius: 10px;
    padding: 15px;
}
@media screen and (max-width: 768px) {
.popup {
        padding: 5px;
        min-width: 90%;
        min-height: 200px;
}
}
.popup h2 {
    margin-top: 50px;
}
.popup .btns {
    margin-top: 50px;
    display: -ms-flex;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}
button {
    border: 0;
    outline: none;
    padding: 8px 10px;
    border-radius: 5px;
    background-color: #ff9967;
    color: #fff;
}
/*覆盖样式*/
.stf-select {
    display: inline-block;
    min-width: 120px;
    text-align: left;
    padding-left: 10px;
}
.stf-select,
.stf-select__inner-wrapper {
    background-color: #ff9967;
    color: #fff;
}



